.portfolio-card {
	display: block;
	box-shadow: 0 5px 20px rgba(0, 0, 0, 0.105);
	background-color: #fff;
	transition: all 0.4s;
	height: 100%;
	position: relative;
	.img-section {
		padding-bottom: 65%;
		background-color: aqua;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		position: relative;
		overflow: hidden;
	}

	.overlay-text {
		color: #fff;
		position: absolute;
		font-size: 14px;
		padding: 5px 16px;
		font-weight: 400;
		background-color: #4d55a2;
		margin-bottom: 5px;
		display: inline-block;
		font-family: $headings-font-family;
		line-height: 1;
	}

	.title {
		transition: all .15s ease;
	}

	.title,
	.detail {
		left: 0;
		bottom: 10px;
	}
	.detail {
		transition: all .45s ease;
		left: 0%;
		transform: translateX(-100%);
	}
	&:hover {
		text-decoration: none;
		.title {
			bottom: 45px;
		}
		.detail {
			transform: translateX(0%);
		}
	}
}
