.service-card {
	padding: 30px 23px 18px 95px;
	position: relative;
	display: block;

	.img-section {
		position: absolute;
	}
	.img-section {
		position: absolute;
		top: 30px;
		left: 25px;
		width: 48px;
		height: 48px;
		background-image: url(./dist/img/icons.png);
		background-size: 48px;
		background-position: center 0px;
	}

	h3,
	p,
	.img-section {
		z-index: 1;
	}

	h3,
	p {
		position: relative;
		font-size: 16px;
		position: relative;
		letter-spacing: -0.02em;
		margin-bottom: 20px;
	}
	p {
		color: #6f6f6f;
	}

	&:after {
		content: "";
		display: block;
		border-radius: 4px;
		background: #fff;
		box-shadow: 0 1px 4px rgba(0, 0, 0, 0.15);
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		transition: 0.3s transform cubic-bezier(0.155, 1.105, 0.295, 1.12), 0.3s box-shadow,
			0.3s -webkit-transform cubic-bezier(0.155, 1.105, 0.295, 1.12);
	}

	&:hover {
		&:after {
			box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
			-webkit-transform: scale(1.05);
			transform: scale(1.05);
		}
	}
}
