@import '../card/portfolio-card.scss';

section.portfolio {
	.items {
		margin: -5px;
		.item {
			padding: 5px;
		}
	}
}

.tags {
	display: flex;
	justify-content: center;
	ul {
		list-style: none;
		display: flex;
		margin: 0;
		padding: 40px 0 60px 0;
		li {
			margin-right: 20px;
		}
		button {
			text-align: center;
			border-radius: 10px;
			text-transform: uppercase;
			font-weight: 600;
			padding: 10px 30px;
			-webkit-transition: all .35s ease;
			transition: all .35s ease;
			border: 0;
			background-color: transparent;
			&.active {
				background-color: $primary;
				color: #fff;
				box-shadow: 0 6px 12px 0 rgba(93, 44, 105, .4);
			}
			&:hover {
				text-decoration: none;
			}
			&:active,
			&:focus {
				outline: 0;
				box-shadow: none;
			}
		}
	}
}
