.lead {
	@include br-text(20, 30);
}
h2,
.section-title {
	position: relative;
	padding-bottom: 6px;
	display: inline-block;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;

	&:after {
		content: " ";
		width: 25px;
		height: 3px;
		background-color: $red;
		position: absolute;
		left: 0;
		bottom: 0;
		transition: width .3s ease-in-out;
	}
}
section:hover {
	.section-title:after {
		width: 100%;
		box-shadow: -1px 3px 13px 0 rgba(204, 88, 88, .6);
	}
}
h3,
.sub-section-title {
	font-size: 22px;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
}
