.banner-slider {
	padding: 150px 0;
	.title-top {
		font-size: 17px;
		font-weight: 400;
		margin: 0;
	}
	.container {
		// padding-top: 150px;
	}
	.title {
	}
	.each-slide {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		line-height: 1.5;
		.img-section {
			background-color: transparent;
			img {
				display: block;
			}
		}
		.text-section {
			padding: 20px 40px 20px 0;
			min-width: 600px;
		}
		h2 {
			font-size: 40px;
			font-weight: 700;
			margin-bottom: 20px;
			font-family: $headings-font-family;
		}
		p {
			margin-bottom: 30px;
			font-family: "Poppins";

			color: #3e3e3e;
			letter-spacing: 0;
			line-height: 30px;
			// @extend .lead;
			font-size: 18px;
		}

		.img-section {
			flex-grow: 1;
			height: auto;
			// width: 500px;
			// height: 400px;
		}

		.cta {
			// display: block;
			padding: 5px 60px;
			font-size: 14px;
		}
	}

	@include media-breakpoint-down(sm) {
		padding: 90px 0 40px 0;
		img {
			width: 220px;
			height: 220px;
			margin: 0 auto;
		}
		.each-slide {
			flex-direction: column-reverse;
			.img-section {
				max-width: 100%;
				height: auto;
			}
			.text-section {
				min-width: unset;
				max-width: unset;
				width: 100%;
			}
		}
	}
}
