.full-width {
	width: 100%;
}

.only-mobile {
	display: none;
}

@include media-breakpoint-down(sm) {
	.only-mobile {
		display: block;
	}

	.only-desktop {
		display: none;
	}
}

.img-section {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-color: #fafafa;
	width: 100%;
	display: block;

	img {
		display: none;
	}
}

.items {
	display: flex;
	flex-wrap: wrap;
	margin: -15px;
}

.item {
	width: 33%;
	padding: 15px;
	animation: fadeInBottom 0.5s ease;

	> div,
	a {
		overflow: hidden;
	}
}

@include media-breakpoint-down(md) {
	.items {
		margin: -10px;
	}

	.item {
		padding: 10px;
		width: 50%;
	}
}

@include media-breakpoint-down(xs) {
	.items {
		margin: -10px -10px;
	}

	.item {
		padding: 10px 15px;
		width: 100%;
	}
}

img {
	max-width: 100%;
	height: auto;
}

body.modal-open .modal {
	display: flex;
	height: 100%;
}

body.modal-open .modal .modal-dialog {
	margin: auto;
}

/* Bootstrap Modal fix"*/
.modal[aria-hidden="true"] {
	z-index: -1;
}

.flickity-page-dots {
	.dot {
		background-color: #d4d4d4;
		transition: all 0.35s;
		opacity: 1;
		&.is-selected {
			background-color: $primary;
		}
	}
}

/* flickity-fade */

.flickity-enabled.is-fade .flickity-slider > * {
	pointer-events: none;
	z-index: 0;
}

.flickity-enabled.is-fade .flickity-slider > .is-selected {
	pointer-events: auto;
	z-index: 1;
}

section {
	padding: 80px 0;
	// padding-top: 90px;
	h2 {
		font-weight: 700;
		margin-bottom: 30px;
	}
	&:focus {
		box-shadow: none;
		outline: 0;
	}
	@include media-breakpoint-down(sm) {
		padding-top: 40px;
	}
}

@keyframes example {
	from {
		opacity: 0.5;
		transform: translateY(-5px);
	}
	to {
		opacity: 1;
		transform: translateY(0px);
	}
}

.hide-it {
	display: none;
}

.item.active {
	display: block;
	animation: example 0.35s ease;
}

@keyframes fading {
	0% {
		opacity: 0.2;
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0.2;
	}
}

/*-*/
footer {
	background-color: #000;
	color: #fff;
	text-align: center;
	padding: 50px 15px;

	.contact-mesg {
		max-width: 550px;
		margin: 0 auto 20px auto;
	}

	@include media-breakpoint-down(sm) {
		padding: 25px 15px;
	}
	.mesg-section {
		max-width: 555px;
		margin: 0 auto;
	}

	.contact-buttons {
		margin-top: 20px;
		display: flex;
		justify-content: center;
		a {
			background-image: none;
			margin-right: 20px;
			&:last-of-type {
				margin-right: 0;
			}
		}
		path {
			fill: #fff;
		}
	}
}

@keyframes move-bg {
	0% {
		transform: translateX(-111%);
	}
	22% {
		transform: translateX(-40%);
	}
	30% {
		transform: translateX(-60%);
	}
	50% {
		transform: translateX(0%);
	}

	65% {
		transform: translateX(50%);
	}

	100% {
		transform: translateX(100%);
	}
}

body {
	> section:not(footer) {
		&:nth-child(odd) {
			background-color: #eaedf2;
		}
	}
}
